<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <!-- 基本信息 -->
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- BOM编号 -->
            <a-form-model-item
              label="BOM编号"
              prop="bomCode"
              ref="bomCode"
            >
              <a-input
                v-model="entity.bomCode"
                placeholder="请输入BOM编号，格式为BOM加4位数字"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 产品选择 -->
            <a-form-model-item
              label="产品"
              prop="productId"
              ref="productId"
            >
              <a-select
                v-model="entity.productId"
                placeholder="请选择产品"
                :disabled="!!ndoEditParams.row"
                @change="handleProductChange"
              >
                <a-select-option
                  v-for="product in productList"
                  :key="product.id"
                  :value="product.id"
                >
                  {{ product.productCode }} - {{ product.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- BOM版本 -->
            <a-form-model-item
              label="BOM版本"
              prop="version"
              ref="version"
            >
              <a-input
                v-model="entity.version"
                placeholder="请输入BOM版本"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 生效日期 -->
            <a-form-model-item
              label="生效日期"
              prop="effectiveDate"
              ref="effectiveDate"
            >
              <a-date-picker
                v-model="entity.effectiveDate"
                style="width: 100%"
                placeholder="请选择生效日期"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="停用">停用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="18">
            <!-- 备注 -->
            <a-form-model-item
              label="备注"
              prop="remark"
              ref="remark"
            >
              <a-textarea
                v-model="entity.remark"
                placeholder="请输入备注信息"
                :rows="2"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 物料清单 -->
      <g-collapse headerName="物料清单">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button
                type="primary"
                icon="plus"
                @click="addMaterial"
                style="margin-bottom: 16px"
              >
                添加物料
              </a-button>
            </a-col>
            <a-col :sm="24">
              <a-table
                :columns="materialColumns"
                :dataSource="entity.materials"
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <!-- 物料编码 -->
                <template slot="materialCode" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.materials[index].materialId"
                    placeholder="请选择物料"
                    style="width: 100%"
                    @change="(value) => handleMaterialChange(value, index)"
                  >
                    <a-select-option
                      v-for="material in materialList"
                      :key="material.id"
                      :value="material.id"
                    >
                      {{ material.materialCode }} - {{ material.name }}
                    </a-select-option>
                  </a-select>
                </template>
                <!-- 数量 -->
                <template slot="quantity" slot-scope="text, record, index">
                  <a-input-number
                    v-model="entity.materials[index].quantity"
                    :min="1"
                    :precision="0"
                    style="width: 100%"
                  />
                </template>
                <!-- 位置 -->
                <template slot="position" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.materials[index].position"
                    placeholder="请输入安装位置"
                  />
                </template>
                <!-- 备注 -->
                <template slot="remark" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.materials[index].remark"
                    placeholder="请输入备注"
                  />
                </template>
                <!-- 操作 -->
                <template slot="action" slot-scope="text, record, index">
                  <a-button
                    type="danger"
                    icon="delete"
                    size="small"
                    @click="() => removeMaterial(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息" >
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditProductBom",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        bomCode: "",
        productId: undefined,
        productCode: "",
        productName: "",
        version: "",
        effectiveDate: null,
        status: "启用",
        remark: "",
        materials: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        bomCode: [
          { required: true, message: "请输入BOM编号", trigger: "change" },
          { pattern: /^BOM\d{4}$/, message: "BOM编号必须为BOM加4位数字", trigger: "change" }
        ],
        productId: [
          { required: true, message: "请选择产品", trigger: "change" }
        ],
        version: [
          { required: true, message: "请输入BOM版本", trigger: "change" }
        ],
        effectiveDate: [
          { required: true, message: "请选择生效日期", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      materialColumns: [
        {
          title: "物料",
          dataIndex: "materialCode",
          width: "30%",
          scopedSlots: { customRender: "materialCode" }
        },
        {
          title: "数量",
          dataIndex: "quantity",
          width: "15%",
          scopedSlots: { customRender: "quantity" }
        },
        {
          title: "安装位置",
          dataIndex: "position",
          width: "20%",
          scopedSlots: { customRender: "position" }
        },
        {
          title: "备注",
          dataIndex: "remark",
          width: "25%",
          scopedSlots: { customRender: "remark" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ],
      productList: [], // 产品列表
      materialList: [], // 物料列表
    };
  },
  
  created() {
    this.fetchProductList();
    this.fetchMaterialList();
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取BOM数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        bomCode: "",
        productId: undefined,
        productCode: "",
        productName: "",
        version: "",
        effectiveDate: null,
        status: "启用",
        remark: "",
        materials: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          materials: row.materials || []
        };
      }
    },
    
    async save() {
      let res = await this.checkError();
      if (res) {
        // 检查物料清单
        if (this.entity.materials.length === 0) {
          this.$message.error("请至少添加一个物料");
          return;
        }

        // 检查物料信息完整性
        const invalidMaterial = this.entity.materials.find(
          material => !material.materialId || !material.quantity
        );
        
        if (invalidMaterial) {
          this.$message.error("请完善物料信息");
          return;
        }

        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.bomCode);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 获取产品列表
    fetchProductList() {
      // 模拟获取产品列表
      setTimeout(() => {
        this.productList = Array.from({ length: 10 }, (_, i) => ({
          id: i + 1,
          productCode: `P${String(i + 1).padStart(4, "0")}`,
          name: `产品${i + 1}`,
        }));
      }, 500);
    },
    
    // 获取物料列表
    fetchMaterialList() {
      // 模拟获取物料列表
      setTimeout(() => {
        this.materialList = Array.from({ length: 20 }, (_, i) => ({
          id: i + 1,
          materialCode: `M${String(i + 1).padStart(4, "0")}`,
          name: `物料${i + 1}`,
        }));
      }, 500);
    },
    
    // 产品选择变更
    handleProductChange(value) {
      const product = this.productList.find(p => p.id === value);
      if (product) {
        this.entity.productCode = product.productCode;
        this.entity.productName = product.name;
      }
    },
    
    // 添加物料
    addMaterial() {
      this.entity.materials.push({
        id: Date.now(),
        materialId: undefined,
        materialCode: "",
        materialName: "",
        quantity: 1,
        position: "",
        remark: "",
      });
    },
    
    // 移除物料
    removeMaterial(index) {
      this.entity.materials.splice(index, 1);
    },
    
    // 物料选择变更
    handleMaterialChange(value, index) {
      const material = this.materialList.find(m => m.id === value);
      if (material) {
        this.entity.materials[index].materialCode = material.materialCode;
        this.entity.materials[index].materialName = material.name;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
</style> 